.intro {
  display: flex;
  margin-top: 4rem;
  justify-content: space-between;
  align-items: center;
  background-color: #121ab0;
  height: 700px;
  /* height: auto; */
  /* background-image: linear-gradient(to right, rgba(166, 167, 169, 0.238), rgb(14, 15, 22)); */
  /* background-image: linear-gradient(to right, rgba(166, 167, 169, 0.238), rgb(14, 15, 22)); */
  background: rgb(114,219,241);
  background: linear-gradient(180deg, rgba(114,219,241,1) 0%, rgba(90,185,215,1) 58%, rgba(0,212,255,1) 100%);
  
  
}

.i-left {
  display: flex;
  position: relative;
  flex-direction: column;
  /* border: 1px solid blue; */
  width: 70%;
}

.i-name {
  display: flex;
  flex-direction: column;
  padding: 70px;
  width: 70%;
  margin-left: 50px;
}

.i-name>:nth-child(1) {
  color: rgb(239, 233, 233);
  font-weight: bold;
  font-size: 2rem;

}

.i-name>:nth-child(2) {
  color: var(--orange);
  font-weight: bold;
  font-size: 2.5rem;

}

.i-name>:nth-child(3) {
  color: var(--gray);
  font-size: 1rem;
  margin-top: 10px;
}

 .Btn{
    border-radius: 34px;
    border: none;
    color: white;
    font-size: 16px;
    padding: 10px;
    margin-bottom: 6px;
    background-color: teal;
    margin-top: 10px;
    padding: 13px;
  }
  .Btn:hover{
    background-color: rgb(31, 109, 109);
    cursor: pointer;
    color: white;
    rotate: 360;
    padding: -px;
  }

#cont {
  color: rgb(239, 233, 233);
}

.i-right{
  /* border: 1px solid red; */
  margin-right: 50px;
  width: 18%;
}

.me {
  width: 100%;
  border-radius: 10%;
  margin: auto;
  height: 355px;
  align-items: center;
 
}

@media screen and (max-width:1024px) {
  .intro{
    display:flex;
    justify-content: space-between;
    margin-top: -2rem;
  }
  .i-left {
    width: 50%;
    font-size: 20px;
  }
  .i-name{
    width: 100%;
  }

  .i-name>:nth-child(1) {
    color: var(--black);
    font-weight: bold;
    font-size: 1rem;

  }

  .i-name>:nth-child(2) {
    font-size: 1.2rem;
  }
  .me {
    width: 80%;
    border-radius: 10px;
    margin: auto;
    display: flex;
    align-items: center;
    height: 200px;
    
  }

}

@media screen and (max-width:600px) {
  .intro {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    margin: auto;
    margin-top: 50px;
    /* margin-left: 100px; */
  }
  .Btn{
    border-radius: 34px;
    border: none;
    color: white;
    font-size: 16px;
    padding: 10px;
    margin-bottom: 6px;
    background-color: teal;
    margin-top: 10px;
  }
  .Btn:hover{
    background-color: rgb(31, 109, 109);
    cursor: pointer;
    color: white;
    rotate: 360;
    padding: -px;
  }
  .i-left {
    width: 100%;
    padding: 0px;
    margin-left: -50px;
    margin-top: -60px;
  }

  

  i.name {
    padding: 0px;
  }

  .me {
 height:250px;
 width:80%;
 margin: auto;

  }

  /* .i-right {
  margin-left:150px;
  margin-bottom:10px;
  margin-top: -50px;
  }  */
  .i-right{
    /* border: 1px solid red; */
    margin-top: -50px;
    margin-left:150px;
    margin-right: 50px;
    width: 50%;
  }
}