.projects {
    // background-image: linear-gradient(to top, #36a0e7 0%, #1771b6 100%);
    // background-image: linear-gradient(to top, #1740b1 0%, #1028a0 100%);
    // padding: 1rem;
    background: rgb(114, 219, 241);
    background: linear-gradient(180deg, rgba(0, 212, 255, 1) 100%, rgba(90, 185, 215, 1) 58%, rgba(114, 219, 241, 1) 0%);

    h2 {
        text-align: center;
        color: #173953;
        font-size: 70px;
        letter-spacing: 2px;
        font-family: sans-serif;
        padding-top: 50px;
    }
}

.projectsdiv {

    div:hover {
        cursor: pointer;
    }
}

.projectsdiv {
    // border: 1px solid;
    width: 90%;
    margin: auto;
    margin-top: 50px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 60px;
    height: auto;
    justify-content: space-between;

    div {
        // background-color: #69beff7b;
        background-color: #d1def0;
        text-align: center;
        padding: 1rem 0;
        border-radius: 10px;
        width: 80%;
        margin: auto;

        img {
            width: 90%;
            padding: 1rem;
            object-fit: cover;
            margin: auto;
        }

        section {
            width: 80%;
            margin: auto;

            h1 {
                font-size: 35px;
                font-weight: 500;
                font-family: sans-serif;
                letter-spacing: 1.2px;
            }

            p {
                margin: 3% 0;
                line-height: 25px;
                opacity: 0.9;
            }

            span {
                h4 {
                    font-size: 24px;
                    font-weight: 400;
                    font-family: sans-serif;
                }

                h5 {
                    padding: .5rem;
                    font-size: 18px;
                    font-weight: 300;
                    font-family: sans-serif;
                    letter-spacing: 1.3px;
                    margin: 2% 0 5%;
                    background-color: #50728ca1;
                    border-radius: 10px;
                }
            }
        }

        div {
            width: 35%;
            padding: 0;
            margin: auto;
            display: flex;
            justify-content: space-around;
            background: transparent;
            border-radius: 0;
           
            .parag {
                // border: 1px solid red;
                font-size: 30px;
            }
            .paragjs {
                // border: 1px solid red;
                font-size: 35px;
            }

            button {
                border: none;
                width: 40px;
                height: 40px;
                border-radius: 50%;
                background: transparent;

                p {
                    color: black;
                    font-size: 15px;
                }
            }
        }
    }

    @media screen and (max-width:900px) {
        grid-template-columns: repeat(1, 1fr);
    }

    @media screen and (max-width:600px) {
        grid-template-columns: repeat(1, 1fr);
        align-items: center;

        // margin-left: -50px;
        .projects {
            background-image: linear-gradient(to top, #36a0e7 0%, #1771b6 100%);
            padding: 1rem;
            // margin: auto;

            // border: 1px solid white;
            h2 {
                text-align: center;
                color: white;
                font-size: 15px;
                letter-spacing: 2px;
                font-family: sans-serif;
            }
        }

        section {
            width: 80%;
            margin: auto;

            h1 {
                font-size: 25px;
                font-weight: 500;
                font-family: sans-serif;
                letter-spacing: 1.2px;
            }

            p {
                margin: 3% 0;
                line-height: 25px;
                opacity: 0.9;
                font-size: 15px;
                text-align: center;
            }

            span {
                h4 {
                    font-size: 5px;
                    // font-weight: 400;
                    font-family: cursive;
                }
                div{
                    .parag {
                        // border: 1px solid red;
                        font-size: 18px;
                    }
                    .paragjs {
                        // border: 1px solid red;
                        font-size: 20px;
                    }
                }
                h5 {
                    padding: .5rem;
                    font-size: 5px;
                    font-weight: 300;
                    font-family: cursive;
                    letter-spacing: 1.3px;
                    margin: 2% 0 5%;
                    background-color: #69beffa1;
                    border-radius: 10px;
                }
            }
        }
    }

}