.contact {
    display: flex;
    justify-content: space-around;
    margin-top: 120px;
}
.vimp{
    /* margin-top: -17px; */
    /* background-image: linear-gradient(to right, rgba(8, 45, 191, 0.803), rgba(3, 14, 175, 0.896)); */
    background: rgb(114, 219, 241);
    background: linear-gradient(180deg, rgba(0, 212, 255, 1) 100%, rgba(90, 185, 215, 1) 58%, rgba(114, 219, 241, 1) 0%);
}
.contact-heading {
    text-align: center;
    font-size: 25px;
    font-weight: bold;
    font-family: sans-serif;
    padding-top: 30px;
}

.dev-left {
    width: 30%;
}

.dev-left>img {
    width: 100%;
}

.email {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.message {
    width: 100%;
    height: 100px;
}

.dev-right {

    width: 40%;
}

.input {
    display: flex;
    justify-content: space-between;
}

.input input {
    padding: 12px;
}

.message {
    margin-top: 40px;
    width: 100%;

}

.message textarea {
    width: 100%;
    height: 150px;
}
#btn_div{
    margin-top: 70px;
}
#btn_div hover{
   cursor: pointer;
}
.btnemail {
    padding: 10px;
    font-size: 25px;
    font-weight: bold;
    font-family: sans-serif;
    width: 40%;
    border-radius: 10px;
    background-color: #ffd447;
    color: white;
}

@media screen and (max-width:600px ) { 
    
    .dev-left{
       
    margin: auto;
        margin-top: 25px;
    }
   .dev-left>img{
    margin: auto;
   }
    .contact-heading {
        padding-top: 70px;
    }
    .contact{
        /* border: 1px solid red; */
        margin-top: -1px;
        display: grid;
        grid-template-columns: repeat(1,1fr);
        
    }
    .btnemail {
        padding: 10px;
        font-size: 25px;
        font-weight: bold;
        font-family: sans-serif;
        width: 70%;
        border-radius: 8px;
        background-color: #ffd447;
        color: white;
    }
    /* form{
       
        width: 90%;
        margin: auto;
    } */
    .dev-right{
        margin-top: 20px;
        width: 90%;
        margin: auto;
    }
    .input{
        display: grid;
        grid-template-columns: repeat(1,1fr);
        width: auto;
    }
    .input>input{
        margin: auto;
        margin-top: 5px;
    }
    .message{
        margin: auto;
        text-align: center;
        margin-top: 5px;
        width: 80%;
    }
    #btn_div{
        margin: auto;
        width: 50%;
    }
    #btn_div>input{
        width:100%;
        margin: auto;
        padding: 4px;
    }
}